import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import product from '@/components/product'
import productList from '@/components/productList'
import productDetails from '@/components/productDetails'
import solution from '@/components/solution'
import solutionList from '@/components/solutionList'
import solutionDetails from '@/components/solutionDetails'
import research from '@/components/research'
import researchList from '@/components/researchList'
import researchDetails from '@/components/researchDetails'
import gyzr from '@/components/gyzr'
import lxzr from '@/components/lxzr'
import zzzs from '@/components/zzzs'
import ryzs from '@/components/ryzs'
import rczp from '@/components/rczp'
import rczpDetails from '@/components/rczpDetails'
import xzfl from '@/components/xzfl'
import zrsh from '@/components/zrsh'
import jchd from '@/components/jchd'
import partner from '@/components/partner'
import zlhz from '@/components/zlhz'
import fwzx from '@/components/fwzx'
import fwzxList from '@/components/fwzxList'
import fwzxDetails from '@/components/fwzxDetails'
import gsxw from '@/components/gsxw'
import xwxq from '@/components/xwxq'
import ldgh from '@/components/ldgh'
import mtbd from '@/components/mtbd'
import scdt from '@/components/scdt'
import gssp from '@/components/gssp'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/', name: 'index', component: index},
    { path: '/product', name: 'product', component: product},//
    { path: '/productList', name: 'productList', component: productList},//
    { path: '/productDetails', name: 'productDetails', component: productDetails},//
    { path: '/solution', name: 'solution', component: solution},//
    { path: '/solutionList', name: 'solutionList', component: solutionList},//
    { path: '/solutionDetails', name: 'solutionDetails', component: solutionDetails},//
    { path: '/research', name: 'research', component: research},//
    { path: '/researchList', name: 'researchList', component: researchList},//
    { path: '/researchDetails', name: 'researchDetails', component: researchDetails},//
    { path: '/gyzr', name: 'gyzr', component: gyzr},//
    { path: '/lxzr', name: 'lxzr', component: lxzr},//
    { path: '/zzzs', name: 'zzzs', component: zzzs},//
    { path: '/ryzs', name: 'ryzs', component: ryzs},//
    { path: '/rczp', name: 'rczp', component: rczp},//
    { path: '/rczpDetails', name: 'rczpDetails', component: rczpDetails},//
    { path: '/xzfl', name: 'xzfl', component: xzfl},//
    { path: '/zrsh', name: 'zrsh', component: zrsh},//
    { path: '/jchd', name: 'jchd', component: jchd},//
    { path: '/partner', name: 'partner', component: partner},//
    { path: '/zlhz', name: 'zlhz', component: zlhz},//
    { path: '/fwzx', name: 'fwzx', component: fwzx},//
    { path: '/fwzxList', name: 'fwzxList', component: fwzxList},//
    { path: '/fwzxDetails', name: 'fwzxDetails', component: fwzxDetails},//
    { path: '/gsxw', name: 'gsxw', component: gsxw},//
    { path: '/xwxq', name: 'xwxq', component: xwxq},//
    { path: '/ldgh', name: 'ldgh', component: ldgh},//
    { path: '/mtbd', name: 'mtbd', component: mtbd},//
    { path: '/scdt', name: 'scdt', component: scdt},//
    { path: '/gssp', name: 'gssp', component: gssp},//
  ]
})


